@Injectable()
export class identifyNodesService{

    constructor(
        // private svg: svgService,
    ){
        // console.log(this.svg);
    }

  

identifyNodes(pathArray){
        // identifiying nodes connected to more than 2 elements
        let tempArray = [];
        for(let path of pathArray){
            tempArray.push(path.nodesOnStart);
        }
        // console.log(tempArray);
        let groups = []; let groups_temp = [];
        for(let path in tempArray){
            let temA = JSON.parse(JSON.stringify(tempArray[path]));
            for(let node of tempArray){
                let ind0 = tempArray[path].indexOf(node[0]) == -1; 
                let ind1 = tempArray[path].indexOf(node[1]) == -1;
                if(ind0 && ind1){
                    
                }else if(ind0 && !ind1){
                    temA.push( node[0]);
                }else if(!ind0 && ind1){
                    temA.push(node[1])
                }else{

                }
            }
            temA = temA.sort();
            let temB = temA.toString();
            if(groups.indexOf(temB) ==-1){
                groups.push(temB);
                groups_temp.push(temA);
            }
            
        } 
        // console.log(groups_temp);
        return groups_temp;
    }

}


